    top: 65px;
    width: 100%;
    /* border: 2px solid red; */
    margin-bottom: 19px;
    padding: 20px

}

/* .container{
    background: linear-gradient(to bottom, lightpink, white);} */

.col {
    /* flex-basis: 70%; */
    max-width: 500px;
}

.col img {
    max-width: 100%;
    padding: 50px 0;

}

.account_page_div{
  /* border: 3px solid red ; */
   /* margin-top: 50px; */
    top: 58px;
    /* left: 50px; */
    width: 100%;
    /* height: 450px;*/
    /* border: 1px solid red; */

}

.form_container {
    /* border: 2px solid red; */
    width: 270px;
    height: 300px;
    background-color: white;
    padding: 20px 0;
    text-align: center;
    /* position: relative; */
    top: 70px;
    /* margin: 20px auto; */
    box-shadow: 1rem 1rem 1rem 0.5rem gray;
    /* margin-bottom: 20px; */
    margin: 30px 20px;

}

.form_container span {
    font-weight: bold;
    padding: 0 10px;
    display: inline-block;
    width: 100px;
    cursor: pointer;
    text-decoration: underline;

}

.form_btn {
    display: inline-block;

}

.form_container form {
    max-width: 300px;

    /* position: absolute; */
    top: 70px;
}

.login_form input {
    width: 80%;
    height: 30px;
    margin: 10px;

    /* border: 1ps solid #ccc; */
}

.login_form button {
    width: 70%;
    background-color: orange;
    outline: black;
    height: 30px;
    border-radius: 20px;
    border: none;
    outline: none;
    margin-top: 30px;

}

.login_form a {
    text-decoration: none;
    color: gray;
}

    
  


@media screen and (max-width: 600px) {

    .form_container {

        margin: 0;
        box-shadow: 0.5rem 0.5rem 0.5rem 0.2rem grey;
        

    }

 
}